import React from 'react';
import './Form.scss';
import Input from '../Input/Input';
import Button from '../Button/Button';
import Title from '../Title/Title';
import AppContext from '../../context';


const types = {
    twitter: 'twitters',
    notes: 'notes',
    articles: 'articles',
}

const descriptions = {
    twitter: 'Twitter description',
    notes: 'Notes description',
    article: 'articles description'
}


class Form extends React.Component {
    state = {
        type: types.twitter,
        title: '',
        link: '',
        image: '',
        description: '',
    }
     

    handleInputChange = e => {
        // console.log([e.target.name])
        this.setState({
            [e.target.name]: e.target.value,
        })

    }


    handleRadioButtonChange = (type) => {
        this.setState({
            type: type,
        })
    }


render() {

const {type} = this.state;

    return (
        <AppContext.Consumer>
        {(context) => (
            <div className="form__wrapper">
        <Title>Dodaj nowy</Title>
        <form className="form__form" onSubmit={ (e)=> context.addItem(e,this.state)}> 
               
            <div className="newButton">
               
                <input className="input" id={types.twitter} checked={type===types.twitter} type='radio' onChange={()=> this.handleRadioButtonChange(types.twitter)}></input>
               <label className="radio" htmlFor={types.twitter}>Twittery</label>

               <input className="input" id={types.articles} checked={type===types.articles} type='radio' onChange={()=> this.handleRadioButtonChange(types.articles)}/>
               <label  className="radio" htmlFor={types.articles}>Artykuły</label>

               <input className="input" id={types.notes} checked={type===types.notes} type='radio' onChange={()=> this.handleRadioButtonChange(types.notes)}/>
               <label className="radio" htmlFor={types.notes}>Notatki</label>

               

               <Button className="radioButton"  click={this.props.close}>X</Button>
               </div>
                <Input
                onChange={this.handleInputChange}
                value={this.state.title}
                name="title"
                required
                label={type==="twitters" ? "Nazwa Twittera" : "Tytuł"}
                />
                
                {type !== types.notes ? (
                    <Input
                    onChange={this.handleInputChange}
                    value={this.state.link}
                    name="link"
                    required
                    label={type ==="twitters" ? "Link do Twittera" : "Link"}
                    /> 
                ) : null}
                
                {type==="twitters" ? <Input onChange={this.handleInputChange} images value={this.state.image} name="image" label="Link do zdjęcia"/> : null }
                
                <Input
                onChange={this.handleInputChange}
                value={this.state.description}
                tag="textarea"
                required
                name="description"
                label={type==="twitters" ? "Dodaj opis do Twittera" : 'Dodaj opis'}
              
                />
            <Button types={this.state.type}>Dodaj nowy post</Button>
        </form>
    </div>
        )}
        
        </AppContext.Consumer>
    )
}
}


export default Form;